import React, {useRef, useEffect} from "react"
import CanvasItem from "../../CanvasItem"
import Css from "../Css.js"
import sdm from "./assets/sdm.mp4"
import { motion } from "framer-motion"

export default function Sdm() {
    const classes = Css()
    const videoRef = useRef(null)

    useEffect(() => {
        if (videoRef !== undefined) {
            videoRef.current.play()
                .catch((err1) => {
                    console.log(err1);

                    videoRef.current.muted = true;
                    videoRef.current.play()
                })
                .catch((err2) => {
                    console.log(err2);
                })
        }
    })

    return (
        <CanvasItem top={155} left={10} width={20} height={30} scrollSpeed={9} className={classes.item}>
            <motion.video ref={videoRef} src={sdm} loop playsInline type="video/mp4" autoPlay muted loop playsInline className={`${classes.video} ${classes.cover}`}/>
        </CanvasItem>
    )
}

